body{
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
}
.header{
  background-color: black;
}
.space {
  margin-top: 70px;
  
}
.space1{
  margin-bottom: 120px;
}
.sect1 {
  background: -webkit-gradient(linear, right top, left top, from(rgb(198, 195, 218)), to(rgba(45, 63, 219, 0.582)));
  background: -webkit-linear-gradient(right, rgb(198, 195, 218), rgba(45, 63, 219, 0.582));
  background: -o-linear-gradient(right, rgb(198, 195, 218), rgba(45, 63, 219, 0.582));
  background: linear-gradient(to left, rgb(198, 195, 218), rgba(45, 63, 219, 0.582));
  
}

.btn0 {
  /*background: linear-gradient(to left, #00CCE4, #4643F6 );*/
  background: -webkit-gradient(linear, right top, left top, from(rgb(68, 87, 255)), to(rgb(54, 12, 241)) );
  background: -webkit-linear-gradient(right, rgb(68, 87, 255), rgb(54, 12, 241) );
  background: -o-linear-gradient(right, rgb(68, 87, 255), rgb(54, 12, 241) );
  background: linear-gradient(to left, rgb(68, 87, 255), rgb(54, 12, 241) );
  
  color: rgb(255, 255, 255);
  -webkit-box-shadow: 0 7px 14px 0 rgba(49,49,93,0.10),
              0 3px 6px 0 rgba(0,0,0,0.08);
          box-shadow: 0 7px 14px 0 rgba(49,49,93,0.10),
              0 3px 6px 0 rgba(0,0,0,0.08);
  border-radius: 30px;
  border: 0;

  font-size: 20px;
  font-weight: 400;
  line-height: 38px;
  outline: none;
  cursor: pointer;
}
.text-gradient {
  color: #f5f5f5 ;
}
.bubble {
  margin: 10px 0 10px 0;
  background: #D6F8C9;
  border-radius: 20px;
  padding: 10px;
  -webkit-box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
          box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
}
.bubble1 {
  background: white;
  border-radius: 20px;
  padding: 10px;
  -webkit-box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
          box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
}
.foo {
  border-top: 3px solid #6C96F1;
  background: black;
}
.foo p {
  color: #FFF76D;
}

/*Responsive */

@media (max-width: 1920px) {
  .font1 {
    font-size: 50px;
  }
}

@media (max-width: 992px) {
  .font1 {
    font-size: 37px;
  }
}

@media (max-width: 768px) {
  .font1 {
    font-size: 30px;
  }
  .space {
    margin-top: 80px;
  }
}

@media (max-width: 575px) {
  .font1 {
    font-size: 20px;
  }
  .space {
    margin-top: 90px;
  }
}

/* ANIMATIONS */
@keyframes fadeInUp {
  from {
      -webkit-transform: translate3d(0,40px,0);
              transform: translate3d(0,40px,0)
  }

  to {
      -webkit-transform: translate3d(0,0,0);
              transform: translate3d(0,0,0);
      opacity: 1
  }
}

@-webkit-keyframes fadeInUp {
  from {
      -webkit-transform: translate3d(0,40px,0);
              transform: translate3d(0,40px,0)
  }

  to {
      -webkit-transform: translate3d(0,0,0);
              transform: translate3d(0,0,0);
      opacity: 1
  }
}

.animated {
  animation-duration: 2s;
  animation-fill-mode: both;
  -webkit-animation-duration: 2s;
  -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
  opacity: 0
}

.fadeInUp {
  opacity: 0;
  animation-name: fadeInUp;
  -webkit-animation-name: fadeInUp;
}   
/* END ANIMATIONS */

/* FORMS */

.StripeElement {
  background-color: white;
  height: 40px;
  padding: 10px 12px;
  border-radius: 4px;
  border: 1px solid transparent;
  -webkit-box-shadow: 0 1px 3px 0 #e6ebf1;
          box-shadow: 0 1px 3px 0 #e6ebf1;
  -webkit-transition: box-shadow 150ms ease;
  -webkit-transition: -webkit-box-shadow 150ms ease;
  transition: -webkit-box-shadow 150ms ease;
  -o-transition: box-shadow 150ms ease;
  transition: box-shadow 150ms ease;
  transition: box-shadow 150ms ease, -webkit-box-shadow 150ms ease;
}

.StripeElement--focus {
  -webkit-box-shadow: 0 1px 3px 0 #cfd7df;
          box-shadow: 0 1px 3px 0 #cfd7df;
}

.StripeElement--invalid {
  border-color: #fa755a;
}

.StripeElement--webkit-autofill {
  background-color: #fefde5 !important;
}



